@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap);

p{
    text-align: center;
    font-size: xx-large;
    color: #2c3e50;
}

p button{
    padding: 15px;
    border-radius: 10px;
    background: #f1c40f;
    color: #2c3e50;
}

p button:hover{
    font-weight: bold;
    padding: 15px 10px;
}
p{
    padding: 1em;
    border-radius: 10px;
    margin: 1em auto 0 auto;
    width: 95%;
    background: #f1c40f;
    color: #2c3e50;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

img {
    border-radius: 100px;
}
